@font-face {
  font-family: PixcelMplus12;
  src: url("../../font/PixelMplus12-Regular.eot");
  src: url("../../font/PixelMplus12-Regular.eot?#iefix") format("embedded-opentype"), url("../../font/PixelMplus12-Regular.woff") format("woff"), url("../../font/PixelMplus12-Regular.ttf") format("truetype"), url("../../font/Pixelmplus12-Regular.svg#svgFontName") format("svg"); }

* {
  box-sizing: border-box; }
  *:before, *:after {
    box-sizing: border-box; }

body {
  font-family: PixcelMplus12, Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  background: #555555; }

header {
  display: flex;
  flex-flow: row;
  flex-wrap: wrap;
  color: #d9edf7;
  background: #555555; }
  header #title {
    flex: 2; }
    header #title a {
      padding: 10px; }
  header #nav ul {
    display: flex;
    flex-flow: row;
    flex-wrap: wrap;
    list-style: none; }
  header #nav li {
    flex: 1; }
    header #nav li a {
      margin: 10px;
      padding: 10px; }
  header a {
    display: inline-block;
    color: #d9edf7;
    text-decoration: none;
    border-radius: 3px; }
    header a.active, header a:hover {
      background-color: #888888; }

a {
  text-decoration: none;
  color: #91C3DC; }

section {
  padding: 20px;
  color: #d9edf7; }

@media screen and (max-width: 730px) {
  header #title, header #nav {
    text-align: center; }
  header #nav {
    width: 100%; }
    header #nav ul {
      margin: 0;
      padding: 0; } }
