.board {
  max-width: 600px;
  margin: 0 auto; }
  .board .row {
    display: flex;
    flex-direction: row;
    justify-content: center; }
    .board .row .xel {
      flex: 1;
      font-size: 10px;
      cursor: pointer;
      position: relative;
      width: 100%; }
      .board .row .xel.debug {
        border: 1px solid gray; }
      .board .row .xel:before {
        content: '';
        display: block;
        padding-top: 100%; }
